import {Component, ElementRef, Input, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {transition, trigger, useAnimation} from '@angular/animations';
import {AdaptDeviceDetectionService} from '../common/device-detection.service';
import {ANIMATION_NAME} from '../common/animation';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularService
} from '../common/radar/adapt-radar';

@Component({
  selector: 'adapt-beard',
  templateUrl: './beard.component.html',
  animations: [
    trigger('contentBlockAnimation', [
      transition(':enter', [
        useAnimation(ANIMATION_NAME.fadeIn)
      ]),
      transition(':leave', [
        useAnimation(ANIMATION_NAME.fadeOut)
      ])
    ])
  ]
})
export class AdaptBeardComponent extends AdaptRadarAngularGenericDirective implements OnInit {
  isMobile: boolean = false;
  isMouseEnter: boolean = false;

  /**
   * Component label
   * @docs-default
   */
  @Input() label: string;

  /**
   * A short description for collapsed view content
   * @docs-default
   */
  @Input() description: string;

  /**
   * Template to be displayed as collapsed content
   * @docs-default null
   */
  @Input() template: TemplateRef<{ $implicit: object | null }> = null;

  /**
   * A context object which can be passed to customize the template output. Please notice, under the hood $implicit key is used
   * @docs-default null
   */
  @Input() tmplContext: object = null;

  @ViewChild('anchor', {static: false}) tmplAnchor: ElementRef<HTMLDivElement>;
  @ViewChild('content', {static: false}) tmplContent: ElementRef<HTMLDivElement>;

  constructor(private _deviceDetector: AdaptDeviceDetectionService,
              _adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptBeard);
  }

  ngOnInit(): void {
    this.isMobile = this._deviceDetector.isMobile();
  }

  onMouseEnter(): void {
    this.isMouseEnter = true;
  }

  onMouseLeave(): void {
    if (this.isMobile) {
      return;
    }

    this.isMouseEnter = false;
  }
}
